	module.exports = {"data":{"markdownRemark":{"html":"<hr>\n<p><img src=\"https://images-na.ssl-images-amazon.com/images/I/91GopjssTaL.jpg\" alt=\"alt text\" title=\"Peace Talks, by Jim Butcher\"></p>\n<p>It took a bit longer than I'd hoped, but I'm back again! </p>\n<p>As promised, I've got some updates related to the new <em>Dresden Files</em> book, as well as another book I'm reading by the same author as the <em>Broken Earth</em> Trilogy (N.K. Jemisin), <em>The City We Became</em>! And as promised, I'll talk a bit about the video lectures that Brandon Sanderson put on Youtube on creative writing.</p>\n<p>A quick update on my novel, <a href=\"http://amazon.com/dp/B0873YKZ5J\"><em>Unbounded</em></a>: I've been steadily getting more readers and more followers online, almost 1500 followers on Twitter, in fact. Thanks so much for all your support!</p>\n<p>The latest installment of the <em>Dresden Files</em> by Jim Butcher, <a href=\"https://www.amazon.com/dp/B07SZLRHMT\"><em>Peace Talks</em></a> was filled with the normal humor, action, fantastical lore, and gritty nail-baiting tension that the series is known for. It was an enjoyable, fast-paced read. But at the same time, it did feel like the book was a revolving door of cameos from the long list of cast members from the series. It was fun to see them all again, but most of the encounters were brief and felt short and a bit forced. I'd still recommend it as a fan of the series, but fair warning: it does very much end in a cliffhanger, so be prepared to wait for the next book to continue the story!</p>\n<p>As for the Brandon Sanderson lectures, I've watched a few of them, and they're highly enjoyable! He's got a really great way of engaging with the audience and making them feel at home. For example, a quote from the first lecure: </p>\n<blockquote>\n<p>If your goal isn't to become a professional writer,, but you like writing, you are welcome here. If you've never written anything in your life, you are welcome here. If you thought you signed up for Chem 107 and came in here and thought \"oh God I'm surrounded by nerds\" you are welcome here!\nIf you have any desire to write or just want to hear from a successful author, I'd highly recommend its stuff!</p>\n</blockquote>\n<p>Finally, I've made a previous post on N.K. Jemisin's <em>Broken Earth</em> trilogy <a href=\"http://www.a-nerds-word.com/broken-earth\">which you can read here</a>, and I'll soon be making another post about her recent book <a href=\"https://www.amazon.com/City-We-Became-N-Jemisin-ebook/dp/B07MFKQDJM\"><em>The City We Became</em></a>. The hook from the Amazon page: <strong>Every great city has a soul. Some are ancient as myths, and others are as new and destructive as children. New York? She's got six.</strong> I haven't finished the book yet, but I've really been loving the story, characters, and world. Plus, as a native New Yorker, it definitely hits the spot! I'll be posting a review for it at some point soon, so stay tuned, and stay safe!</p>\n<p>Until next time,</p>\n<p>-- Bradley</p>","frontmatter":{"date":"August 16, 2020","path":"/long-awaited-updates-part-2","title":"Long Awaited Updates Part 2: Unbounded, Peace Talks, Brandon Sanderson, and <i>The City We Became</i>","tags":["Life","Book Review","Personal","Writing"]}}},"pathContext":{}}

